.header {
	min-width: 300px;
	height: 100vh;
	background-image: url(/images/photos/Cover_photo.jpg);
	background-size: cover;
	background-position: center;

	&__container {
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100vh;
		text-align: center;
		padding: 30px 20px 90px;

		@include onTablet {
			padding: 40px 72px 130px;
		}
	
		@include onDesktop {
			padding: 40px 120px 120px;
		}
	}

	&__title {
		@extend %h1;

		@include onTablet {
			font-size: 64px;
		}

		@include onDesktop {
			font-size: 80px;
		}
	}
}
